<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      setting: {
        invite_friends: '',
      },
      file: [],
      file_user: [],
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'web',
    })
      .then(r => {
        this.setting = r.data
        this.loading = false

        if (r.data.logo) {
          this.file = [
            {
              url: r.data.logo,
              name: r.data.logo,
            },
          ]
        }
        if (r.data.user_logo) {
          this.file_user = [
            {
              url: r.data.user_logo,
              name: r.data.user_logo,
            },
          ]
        }
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch('/sys/config/update', {
        type: 'web',
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    fileSuccess(res, file) {
      this.file = [file]
      this.setting.logo = res.data.file
    },
    fileSuccess1(res, file) {
      this.file_user = [file]
      this.setting.user_logo = res.data.file
    },
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
}
</script>
